export default function transformToNanotons(input) {
  // Преобразуем число в строку
  let inputString = input.toString();

  // Если число целое, добавляем 9 нулей
  if (inputString.indexOf(".") === -1) {
    return inputString + "000000000";
  }

  // Иначе определяем количество цифр после точки
  let digitsAfterDot = inputString.length - inputString.indexOf(".") - 1;

  // Добавляем соответствующее количество нулей
  let result = inputString.replace(".", "") + "0".repeat(9 - digitsAfterDot);

  return result;
}
